import React from "react";
import animine from "./style/animine.mp4";
import "./style/projects.css";

function Project() {
  return (
    <div className="projects">
      <h3>Animine</h3>
      <div className="project">
        <video className="project-iframe" id="animine" autoPlay loop muted>
          <source src={animine} />
        </video>
        <h1>This is a website </h1>
      </div>
      <hr />
      <h3>Animine</h3>
      <div className="project">
        <h1>This is a website </h1>
        <iframe
          title="animine"
          className="project-iframe"
          src="https://animine.netlify.app/"
          frameborder="0"
        ></iframe>
      </div>
    </div>
  );
}

export default Project;
